import React from "react"
import cssVar from "views/mobileView/styles"

const Steps = ({ stepsArray }) => {

    React.useEffect(()=>{
        document.getElementById('sideline').style.marginBottom = document.getElementById('abcd').lastChild.offsetHeight+'px'
    },[])
    
    return <div style={{ width: "90%", display: 'flex' }}>
        <div id="sideline" style={{ background: 'linear-gradient(#58985F,#C2DC55)', position: 'relative', left: '12px', top: '24px', width: '3px' }}></div>
        <div id="abcd">
            {
                stepsArray.map((s, i) => {
                    return <div key={i} style={{ display: "flex", marginTop: "6%" }} className='indexStep'>
                        <div className='solidCircleNumber' style={{ marginRight: '16px' }}>{i + 1}</div>
                        <div style={{ flex: 1 }}>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--darkGreen') }}>{s.title}</div>
                            <div className='smallTextLight'>{s.description}</div>
                        </div>
                    </div>
                })
            }
        </div>
    </div>
}

export default Steps