import React from 'react';
import { AiOutlineDown } from 'react-icons/ai';
import { BsBookmark, BsBookmarkFill } from 'react-icons/bs';
import { FiArrowLeft } from "react-icons/fi";
import { HiOutlineShare } from 'react-icons/hi';
import cssVar from 'views/mobileView/styles';

const MainHeader = ({
    header,
    navigation,
    screenType,
    backAction=()=>{navigation.goBack()},
    right,
    rightAction = () => {},
    isWatchlisted = false,
    onWatchlistIconClick = ()=>{},
    onShareIconClick = ()=>{},
    onHeaderClick = ()=>{}
}) => {
    
    const renderRightSide = () => {
        switch (right) {
            case 'bookmark-share':
                return (
                    <span>  
                        {
                            isWatchlisted?
                            <BsBookmarkFill
                                className='secondaryGreen'
                                size={24}
                                onClick={onWatchlistIconClick}
                            />
                            :
                            <BsBookmark
                                className='secondaryGreen'
                                size={24}
                                onClick={onWatchlistIconClick}
                            />
                        }
                        <HiOutlineShare
                            className='secondaryGreen'
                            size={24}
                            style={{marginLeft:"20px"}}
                            onClick={onShareIconClick}
                            />
                    </span>
                );
            case 'tip': 
                    return <span className='header3dark secondaryGreen' onClick={()=> rightAction()}>Tip <AiOutlineDown size={10}/></span>
            default:
                if(!right){
                    return <></>
                } else {
                    return (
                        <span className='greenChipHeader' onClick={()=>{ rightAction ()}}>
                            {right}
                        </span>
                    );
                }
        }
    };

    return (
        <div className="mainHeader sticky-top">
            {screenType !== 'Home' && (
                <div style={{marginRight:"4%",minWidth:"8%"}}>
                    <FiArrowLeft
                    color={cssVar.getPropertyValue("--secondaryGreen")}
                    size={24} onClick={() => backAction()} />
                </div>
            )}
            <div style={{flex:1}}>
                <span className="screenTitle" onClick={()=>onHeaderClick()}>{header}</span>
            </div>
            <div style={{ display: "flex" }}>
                {renderRightSide()}
            </div>
        </div>
    );
};

export default MainHeader;